import Vue from "vue";
import Vuex from "vuex";
// import products from "@/data/products"; // локальный
import axios from "axios";
import { API_BASE_URL } from "../config";

Vue.use(Vuex);

export default new Vuex.Store({
  //состояние св-ва для хранения списка товаров в корзине
  state: {
    cartProducts: [
      // { productId: 1, amount: 2 } //1-это товар по id 2-количество товара в корзине
    ],
    userAccessKey: null, // уникальный ключ пользователя

    cartProductsData: [], // данные о товаре с сервера

    cartLoading: false, // хранит статус загрузки

    cartLoadingFailed: true, //хранит произошла ли ошибка при загрузки

    orderInfo: null,//ответ с сервера по поводу оформления заказа
  },
  mutations: {
    //мутация добавления данных с сервера по поводу оформления заказа
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },

    //очистка корзины после отправки формы на сервер
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },

    //мутация, обработчик функция добавления товара в корзину
    // addProductToCart(state, { productId, amount }) {
    //   const item = state.cartProducts.find(item => item.productId === productId);

    //   if (item) {
    //     item.amount += amount;

    //   } else {
    //     state.cartProducts.push({
    //       productId,
    //       amount,
    //     });
    //   }
    // },
    //Первоначальный вариант
    // addProductToCart(state, payLoad) {
    //   state.cartProducts.push({
    //     productId: payLoad.productId,
    //     amount: payLoad.amount,
    //   });
    // }
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find(item => item.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter(item => item.productId !== productId);
    },
    // мутация обновления уникального ключа пользователя
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    // мутация записи данных о продукте с сервера
    updateCartProductsData(state, items) {
      state.cartProductsData = items;
    },
    // мутация синхронизируем данные из API с массивом оригинальном свойстве cartProducts
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map(item => {
        return {
          productId: item.product.id,
          amount: item.quantity
        }
      });
    },
    // мутация обновления загрузки
    updateCartLoading(state) {
      state.cartLoading = !state.cartLoading;
    },
    // мутация обновления ошибки
    updateCartLoadingFailed(state) {
      state.cartLoadingFailed = !state.cartLoadingFailed;
    },
  },
  //геттер - вычислительное свойство
  getters: {
    //функция, которая в хранилище подмешает нужную информацию о продукте
    cartDetailProducts(state) {
      return state.cartProducts.map(item => {
        const product = state.cartProductsData.find(p => p.product.id === item.productId).product;

        return {
          ...item,
          product: {
            ...product,
            image: product.image.file.url
          }
          // product: products.find(p => p.id === item.productId) // из локального
        }
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, item) => (item.product.price * item.amount) + acc, 0);
    },
    cartTotalProduct(state) {
      return state.cartProducts.length;
    }
  },
  actions: {
    //действие которое будет загружать данные о заказе по индификатору пользователя
    loadOrderInfo(context, orderId) {
      return axios.get(API_BASE_URL + '/api/orders/' + orderId, {
        params: {
          userAccessKey: context.state.userAccessKey
        }
      })
        .then(response => {
          context.commit('updateOrderInfo', response.data)
        });
    },

    //return возвращает промессы
    loadCart(context) {
      context.commit('updateCartLoading');
      context.commit('updateCartLoadingFailed');

      return axios
        .get(API_BASE_URL + '/api/baskets', {
          params: {
            userAccessKey: context.state.userAccessKey
          }
        })
        .then(response => {
          //если значение ключа нет тогда его записываем в хранилище
          if (!context.state.userAccessKey) {
            localStorage.setItem('userAccessKey', response.data.user.accessKey); //записываем в локальное хранилище данные с запроса о уникальном ключе
            context.commit('updateUserAccessKey', response.data.user.accessKey);
          }


          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        }).catch(() => context.commit('updateCartLoadingFailed')).then(() => context.commit('updateCartLoading')); // при успешном ответе с сервера будем вызывать мутации которые изменят значения свойств состояния
    },
    //действие добавление товара в корзину
    addProductToCart(context, { productId, amount }) {
      return (new Promise(resolve => setTimeout(resolve, 2000)))
        .then(() => {
          return axios.post(API_BASE_URL + '/api/baskets/products', {
            // передаем данные для добавление в корзину товара
            productId: productId,
            quantity: amount,
          }, {
            // передаем уникальный ключ
            params: {
              userAccessKey: context.state.userAccessKey
            }
          })
            .then(response => {
              context.commit('updateCartProductsData', response.data.items);
              context.commit('syncCartProducts');
            })
        })
    },
    //действие взаимодействия с API
    updateCartProductAmount(context, { productId, amount }) {
      context.commit('updateCartProductAmount', { productId, amount });

      if (amount < 1) {
        return;
      }

      return axios.put(API_BASE_URL + '/api/baskets/products', {
        // передаем данные для добавление в корзину товара
        productId: productId,
        quantity: amount,
      }, {
        // передаем уникальный ключ
        params: {
          userAccessKey: context.state.userAccessKey
        }
      })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        })
    },
    //удаление товара из корзины
    deleteCartProduct(context, productId) {
      context.commit('deleteCartProduct', productId);

      return axios.delete(API_BASE_URL + '/api/baskets/products', {
        // передаем уникальный ключ
        params: {
          userAccessKey: context.state.userAccessKey,
        },
        data: {
          productId,
        }
      })
        .then(response => {
          context.commit('updateCartProductsData', response.data.items);
          context.commit('syncCartProducts');
        })
    }
  }
});
