<script>
import gotoPage from "@/helpers/gotoPage";
import numberFormat from "@/helpers/numberFormat";
import router from "@/router";

import ProductAmount from "@/components/ProductAmount.vue";

import axios from "axios";
import { API_BASE_URL } from "../config";

import { mapActions } from "vuex";

export default {
  data() {
    return {
      productAmount: 1,
      productData: null,
      productLoading: false,
      productLoadingFailed: false,

      productAdded: false, //св-во добавился товар или нет
      productAddSending: false, //св-во что товар отправляется в корзину
    };
  },

  filters: {
    numberFormat,
  },
  computed: {
    product() {
      return this.productData;
      // return products.find((product) => product.id === +this.$route.params.id); // знак + приводит строку к числу
    },
    category() {
      return this.productData.category;
      // return categories.find((category) => category.id === this.product.categoryId);
    },
  },
  methods: {
    ...mapActions(["addProductToCart"]),

    gotoPage,
    addToCart() {
      this.productAdded = false;
      this.productAddSending = true;

      this.addProductToCart({
        productId: this.product.id,
        amount: this.productAmount,
      }).then(() => {
        this.productAdded = true;
        this.productAddSending = false;
      });

      // this.$store.commit("addProductToCart", {
      //   productId: this.product.id,
      //   amount: this.productAmount,
      // });
    },
    loadProduct() {
      this.productLoading = true;
      this.productLoadingFailed = false;
      axios
        .get(API_BASE_URL + "/api/products/" + +this.$route.params.id)
        .then((response) => (this.productData = response.data))
        .catch(() => (this.productLoadingFailed = true))
        .then(() => (this.productLoading = false));
    },
  },
  components: { router, ProductAmount },

  watch: {
    "$route.params.id": {
      // обработчик будет срабатывать сразу при создании компонента а так же если изменится динамический сегмент $route.params.id
      handler() {
        this.loadProduct();
      },
      immediate: true,
    },
  },
  // теперь метод вызывается и при изменении и при создании компонента

  // Альтернативная запись
  // created() {
  //   this.loadProduct();
  // },
  // watch: {
  //   "$route.params.id"() {
  //     this.loadProduct();
  //   },
  // }
};
</script>
