<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">Каталог</h1>
      <span class="content__info"> 152 товара </span>
    </div>

    <div class="content__catalog">
      <ProductFilter
        :price-from.sync="filterPriceFrom"
        :price-to.sync="filterPriceTo"
        :category-id.sync="filterCategory"
        :color.sync="filterColor"
      />

      <section class="catalog">
        <div v-if="productsLoading">
          Загрузка товаров<img src="../../public/img/Full snake.gif" />
        </div>

        <div v-if="productsLoadingFailed">
          Произошла ошибка при загрузки
          <button @click.prevent="loadProducts">Попробовать еще раз</button>
        </div>

        <ProductList v-bind:products="products" />
        <!-- <ProductList v-bind:products="products"></ProductList> общая запись,
          если между откр. и закр. ничего нет можем использовать укороченную запись -->

        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage" />
      </section>
    </div>
  </main>
</template>

<script>
import ProductList from "@/components/ProductList.vue";
import BasePagination from "@/components/BasePagination.vue";
import ProductFilter from "@/components/ProductFilter.vue"; // @-путь с папки src
import axios from "axios";
import { API_BASE_URL } from "../config"; //сделано что бы вдруг путь поменяется  и можно поменять только в одном месте и везде сменится

export default {
  components: { ProductList, BasePagination, ProductFilter }, // Общая запись ProductList: ProductList а используем укороченную т.к. имена одинаковые
  data() {
    return {
      filterPriceFrom: 0, //цена фильтра от
      filterPriceTo: 0, //цена фильтра до
      filterCategory: 0, // индификатор выбранной категории
      filterColor: "", //фильтр по цвету

      page: 1, //номер страницы
      productsPerPage: 3, // кол. товара оттображ. на странице
      // products,  products: products - общая запись, используем укороченную т.к. имена совпадают

      productsData: null, //св-во в состоянии которого хранит ответ от сервера

      productsLoading: false, // хранит статус загрузки

      productsLoadingFailed: false, //хранит произошла ли ошибка при загрузки
    };
  },
  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => {
            return {
              ...product,
              image: product.image.file.url, // чтобы картинки правильно отображались
            };
          })
        : [];
      //Переключились на данные от сервера а не локального
      // const offset = (this.page - 1) * this.productsPerPage;
      // return this.filteredProducts.slice(offset, offset + this.productsPerPage); //вычисляемое свойство которое будет возвращать часть товара которое должны показывать в зависимости от пагинации
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;

      // return this.filteredProducts.length;
    },
  },

  methods: {
    //метод загружает данные с сервера
    loadProducts() {
      this.productsLoadingFailed = false;
      this.productsLoading = true;
      //позволяет уменьшить кол-во вызовов к серверу при изменении
      clearTimeout(this.loadProductsTimer);
      //св-во экземпляра компонента а не св-во состояния компонента
      this.loadProductsTimer = setTimeout(() => {
        axios
          .get(API_BASE_URL + `/api/products`, {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategory,
              colorId: this.filterColor,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
            },
          })
          .then((response) => (this.productsData = response.data))
          .catch(() => (this.productsLoadingFailed = true))
          .then(() => (this.productsLoading = false));
      }, 5000);
    },
  },
  //наблюдатель, следит за изменением св-ва page (где хранится текущая страница)
  watch: {
    page() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
    filterCategory() {
      this.loadProducts();
    },
    filterColor() {
      this.loadProducts();
    },
  },

  // метод загрузки будем вызывать с сервера, в тот момент когда компонент создается через хук
  created() {
    this.loadProducts();
  },
};
</script>
