import BaseFormField from '@/components/BaseFormField.vue';

export default {
  props: ['title', 'error', 'placeholder', 'value'],
  components: { BaseFormField },
  computed: {
    //создаем двунаправленное вычисляемое св-во для v-model="formData.name (мы не можем менять входные параметры на прямую)
    dataValue: {
      get() {
        return this.value;
      },
      set(value) {
        this.$emit('input', value); //выкидываем событие с новым событием
      }
    }
  }
}
