<template>
  <div class="item__row">
    <div class="form__counter">
      <button type="button" aria-label="Убрать один товар" @click="amountMinus(amount)">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" />

      <button type="button" aria-label="Добавить один товар" @click="amountPlus(amount)">
        <svg width="12" height="12" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <button class="button button--primery" type="submit" :disabled="productAddSending">
      В корзину
    </button>
  </div>
</template>

<script>
export default {
  model: {
    prop: "number",
    event: "amountProduct",
  },
  props: ["amount", "productAddSending"],
  methods: {
    amountPlus(number) {
      this.$emit("amountProduct", number + 1);
      // this.amount = amount + 1;
    },
    amountMinus(number) {
      if (this.amount > 1) {
        this.$emit("amountProduct", number - 1);
      }
    },
  },
};
</script>
