<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.image" width="120" height="120" alt="item.product.title" />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code"> Артикул: {{ item.productId }} </span>

    <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар" @click="amountMinus(item.amount)">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count" />

      <button type="button" aria-label="Добавить один товар" @click="amountPlus(item.amount)">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div>

    <b class="product__price"> {{ (item.amount * item.product.price) | numberFormat }} ₽ </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины"
      @click.prevent="deleteProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";
// import { mapMutations } from "vuex"; //вспомогательная функция, проксируем обработчик мутации

export default {
  filters: { numberFormat },
  props: ["item"],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch("updateCartProductAmount", {
          productId: this.item.productId,
          amount: value,
        });
      },
    },
  },
  methods: {
    // ...mapMutations({ deleteProduct: "deleteCartProduct" }),
    // deleteProduct(productId) {
    //   this.$store.commit('deleteCartProduct', productId)
    // }
    deleteProduct(productId) {
      this.$store.dispatch("deleteCartProduct", productId);
    },

    amountPlus(value) {
      this.$store.dispatch("updateCartProductAmount", {
        productId: this.item.productId,
        amount: value + 1,
      });
    },
    amountMinus(value) {
      if (value > 1)
        this.$store.dispatch("updateCartProductAmount", {
          productId: this.item.productId,
          amount: value - 1,
        });
    },
  },
};
</script>
