import Vue from "vue";
import VueRouter from "vue-router";
import MainPage from "@/pages/MainPage.vue";
import ProductPage from "@/pages/ProductPage.vue";
import CartPage from "@/pages/CartPage.vue";
import OrderPage from "@/pages/OrderPage.vue"
import OrderInfoPage from "@/pages/OrderInfoPage.vue"
import NotFoundPage from "@/pages/NotFoundPage.vue";

Vue.use(VueRouter);

const routes = [
  { name: 'main', component: MainPage, path: '/' }, // main-имя component-компонент который используется(отрисовывается path-путь
  { name: 'product', component: ProductPage, path: '/product/:id' }, // динамические сегменты (путь будет постоянно меняться т.к id товара будет меняться)
  { name: 'cart', component: CartPage, path: '/cart' },
  { name: 'order', component: OrderPage, path: '/order' },
  { name: 'orderInfo', component: OrderInfoPage, path: '/order/:id' },
  { name: 'notFound', component: NotFoundPage, path: '*' }, // *- звездочка означает что любой значение адреса будет совпадать с этим путем. те. если не один из пред. маршрутов гн будет соответствовать тогда будет страница не найдена
];

const router = new VueRouter({
  routes
});

export default router;
