<script>
import BaseFormTextarea from '@/components/BaseFormTextarea.vue';
import BaseFormText from '@/components/BaseFormText.vue';
import BaseFormField from '@/components/BaseFormField.vue'; // не используем

//для оформления правой части подключаем
import numberFormat from '@/helpers/numberFormat';
import { mapGetters } from 'vuex';
import router from '@/router';

import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default ({
  data() {
    return {
      //хранится информация значения полей с формы, значения добавляются через v-model
      formData: {},
      formError: {},
      formErrorMessage: '',

      productLoading: false,  //св-во отправилась форма заказа товара или нет
      productLoadingFailed: false, //св-во что товар отправляется

    }
  },
  components: { BaseFormField, BaseFormText, BaseFormTextarea, router },
  filters: { numberFormat },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
      totalProduct: "cartTotalProduct",
    })
  },
  methods: {
    order() {
      this.formError = {}
      this.formErrorMessage = ''

      this.productLoading = true;
      this.productLoadingFailed = false;

      axios.post(API_BASE_URL + '/api/orders', {
        ...this.formData
      }, {
        params: {
          userAccessKey: this.$store.state.userAccessKey
        }
      },
      )
        .then(response => {
          this.$store.commit('resetCart'); // сброс локального хранилища
          this.$store.commit('updateOrderInfo', response.data); //внесение данных в хранилище по поводу оформления
          this.$router.push({ name: 'orderInfo', params: { id: response.data.id } }); //перенаправление пользователя
        })
        .catch(error => {
          this.formError = error.response.data.error.request || {};
          this.formErrorMessage = error.response.data.error.message;
          this.productLoadingFailed = true;
        })
        .then(() => (this.productLoading = false));
    }
  }
})
</script>
